.timeline {
  margin: 30px 0;
}

.timeline-item {
  padding: 7px 30px;
  position: relative;
  border-left: 2px solid #bdbdbd;
}
.timeline-item p {
  font-size: 1rem;
}
.timeline-item::before {
  content: attr(date-is);
  position: absolute;
  left: 2em;
  font-weight: bold;
  top: 1em;
  display: block;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 0.785rem;
}
.timeline-item::after {
  width: 20px;
  height: 20px;
  display: block;
  top: 1em;
  position: absolute;
  left: -12px;
  border-radius: 10px;
  content: "";
  border: 2px solid #bdbdbd;
  background: #bdbdbd;
}

.timeline-heading {
  font-weight: bold;
}

.timeline-date {
  background-color: #bdbdbd2d;
  display: inline-block;
  padding: 5px 15px;
  margin: 10px 0;
  border-radius: 3px;
}
